.switch {
  position : relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  background-color: #eee;
  border-radius: 20px;
}

.switch::after {
  /* content: ''; */
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: red;
  top: 1px;
  left: 1px;
  transition: all 0.3s;
} 

.checkbox:checked + .switch::after {
  left: 20px;
}

.checkbox:checked + .switch {
  background-color: #7983ff;
}

